@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400&display=swap);
body {
  margin: 0;
  font-family: "Roboto Condensed", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Roboto Condensed", sans-serif;
}

/* font-family: 'Roboto Condensed', sans-serif */

:root {
  font-size: 16px;
  --primary-bg: #ffb52f;
  --secondary-bg: #001232;
}

* {
  box-sizing: border-box;
}

:root::-webkit-scrollbar {
  width: 10px;
  border-radius: 5px;
}

:root::-webkit-scrollbar-thumb {
  background-color: #001232;
}

.p-20 {
  padding: 20px;
}

.w-90 {
  width: 90%;
  margin: auto;
  padding: 20px 0px;
}

.MuiSnackbarContent-message {
  font-family: "Roboto Condensed", sans-serif !important;
}

.MuiFormHelperText-root {
  font-family: "Roboto Condensed", sans-serif !important;
}

.MuiDialogTitle-root > *,
.MuiDialogContent-root > *,
.MuiDialogActions-root > * {
  font-family: "Roboto Condensed", sans-serif !important;
}

.MuiDialogActions-root .MuiButton-textPrimary {
  color: #001232;
  font-weight: bold;
}

.notfoundImg {
  width: 800px;
  height: 500px;
  margin: auto;
  display: block;
}

@media only screen and (max-width: 780px) {
  .notfoundImg {
    width: 100%;
    height: auto;
  }
}

.wrap-header {
  font-family: "Roboto Condensed", sans-serif;
  background-color: #fff;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.04);
  position: relative;
  padding: 30px;
}

.header .navbar {
  display: flex;
  flex-direction: row;
  list-style: none;
  background-color: var(--secondary-bg);
  padding: 20px;
  margin: 0;
  justify-content: space-between;
}

.header .navbar .nav-item .nav-link {
  margin: 10px;
  text-decoration: none;
}

.header .navbar .nav-item .nav-link .nav-text {
  color: #bdbdbd;
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: bold;
}

.header .right-side {
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header .login-form-button {
  padding: 15px;
  border: 1px solid #111111;
}

.cart-box {
  font-weight: bold;
  font-size: 1.3rem !important;
  background-color: var(--primary-bg);
  display: flex;
  font-family: "Roboto Condensed", sans-serif;
  color: #2f2f2f;
  padding: 15px;
}

.cart-box .pricing {
  margin-left: 8px;
}

.awssld__content img {
  width: auto !important;
  height: auto !important;
  position: static !important;
  width: 800px !important;
  object-fit: contain;
}

.awssld__content {
  background-image: url(/static/media/slider-bg.121ebcd3.jpg);
}

.awssld__bullets button {
  width: 10px !important;
  height: 10px !important;
}

.section1 {
  font-family: "Roboto Condensed", sans-serif;
  padding: 3% 15%;
}

.section1 p {
  line-height: 2;
  font-weight: 400;
  font-size: 1.3rem;
}

.question-contact {
  display: flex;
  margin-top: 50px;
}

.question-contact .question-icon {
  padding: 15px 25px 15px 25px;
  font-size: 1.2rem;
  font-weight: bold;
  background-color: var(--primary-bg);
}

.question-contact .question-content-number {
  padding: 15px 25px 15px 25px;
  background-color: var(--secondary-bg);
  color: #ffffff;
  font-weight: bold;
  font-size: 1.2rem;
}

.section2 {
  padding: 3% 15%;
  justify-content: center;
  font-family: "Roboto Condensed", sans-serif;
  color: #ffffff;
}

.section2 .banner {
  /* background: url("http://preview.hasthemes.com/oswan/assets/img/banner/banner-3.jpg"); */
  width: 100%;
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.section2 .banner .square h1 {
  font-size: 1.5rem;
}

.section2 .square {
  border: 1px solid #ffffff;
  padding: 60px;
  transition: all 0.2s ease;
}

.section2 .banner:hover .square {
  padding: 80px;
}

.section2 .banner:hover h1 {
  -webkit-animation-name: zoomInzoomOut;
          animation-name: zoomInzoomOut;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes zoomInzoomOut {
  from {
    font-size: 0.9rem;
  }
  to {
    font-size: 1.8rem;
  }
}

@keyframes zoomInzoomOut {
  from {
    font-size: 0.9rem;
  }
  to {
    font-size: 1.8rem;
  }
}

.section3 {
  padding: 3% 15%;
  text-align: center;
}

.section3 h1 {
  font-size: 2.5rem;
  font-family: "Roboto Condensed", sans-serif;
}

.section3 > p {
  width: 50%;
  margin: auto;
  line-height: 1.5;
  font-size: 1.2rem;
  margin: 30px auto 30px auto;
}

.section3 .normal-button {
  padding: 15px 30px;
  font-size: 1.2rem;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: bold;
  margin: 10px;
  text-transform: uppercase;
  border: none;
  outline: none;
  background-color: #ffffff;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.section3 .normal-button.primary {
  background-color: var(--primary-bg);
}

.product-item-style-list {
  margin: 15px;
  font-family: "Roboto Condensed", sans-serif;
  cursor: pointer;
  box-shadow: 0 2px 8px 0.5px rgba(0, 0, 0, 0.22);
}

.product-item-style-list .img-cover {
  position: relative;
}

.product-item-style-list img {
  width: 100%;
  height: 100%;
}

.product-item-style-list .top-intro {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 30px;
  text-align: center;
  color: #9b9b9b;
  font-size: 0.9rem;
  font-weight: bold;
}

.product-item-style-list .bottom-intro {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 50px;
  display: flex;
  justify-content: space-around;
  width: 100%;
  visibility: hidden;
}

.product-item-style-list:hover .bottom-intro {
  visibility: visible;
}

.product-item-style-list .bottom-intro div {
  border: 1px solid #e8e8e8;
  padding: 10px;
  transition: 0.3s;
}

.product-item-style-list .bottom-intro div:hover {
  background-color: var(--primary-bg);
}

.product-item-style-list .infor-product {
  padding: 15px;
}

.product-item-style-list .infor-product .name {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  font-size: 1.2rem;
  font-weight: bold;
}

.product-item-style-list .infor-product .pricing {
  margin-bottom: 20px;
  font-size: 1.2rem;
  font-weight: bold;
}

.product-item-style-list .infor-product .description {
  margin: 30px 0px;
  line-height: 1.5;
}

.product-item-style-list .infor-product .add-to-cart {
  padding: 10px 20px;
  text-transform: uppercase;
  background-color: #ffffff;
  border: 1px solid #ffb52f;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: bold;
  color: #333;
  transition: 0.3s;
}

.product-item-style-list .infor-product .add-to-cart:hover {
  background-color: #ffb52f;
  color: #ffffff;
}

.product-item-style-list .infor-product .add-to-cart .ti-shopping-cart {
  margin-right: 10px;
}

@media only screen and (max-width: 599px) {
  .product-item-style-list img {
    height: 500px;
  }

  .product-item-style-list .infor-product .description {
    font-size: 0.9rem;
  }

  .product-item-style-list .infor-product .add-to-cart {
    width: 100%;
  }

  .product-item-style-list .infor-product {
    padding: 10px;
  }

  .product-item-style-list .top-intro,
  .product-item-style-list .infor-product .name,
  .product-item-style-list .infor-product .pricing {
    font-size: 0.9rem;
  }
}

.product-item {
  position: relative;
  font-family: "Roboto Condensed", sans-serif;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  margin: 15px;
  box-shadow: 0 2px 8px 0.5px rgba(0, 0, 0, 0.22);
  cursor: pointer;
}

.product-item:hover {
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.product-img {
  display: block;
  width: 100%;
}

.product-item .intro-list {
  display: flex;
  justify-content: space-evenly;
  padding: 30px;
  color: #9b9b9b;
  font-size: 0.9rem !important;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  font-weight: 600;
}

.product-item:hover .product-content-wrapper {
  height: 0;
  padding: 0;
}

.product-item .product-content-wrapper {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 15px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  font-weight: bold;
  overflow: hidden;
}

.product-item:hover .action-button-group {
  visibility: visible;
}

.action-button-group {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 50px 30px;
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  visibility: hidden;
}

.action-button-group span {
  border: 1px solid #e8e8e8;
  padding: 15px;
  font-weight: bold;
}

.action-button-group span:hover {
  background-color: var(--primary-bg);
}

footer {
  background-color: var(--secondary-bg);
  padding-bottom: 98px;
  padding-top: 210px;
  font-family: "Roboto Condensed", sans-serif;
}

footer p {
  color: #919191;
}

footer .for-support {
  color: #b6b6b6 !important;
  font-weight: bold;
  font-size: 1.3rem;
  margin-top: 50px;
}

footer .for-support p {
  color: #b6b6b6 !important;
}

footer .quick-link {
  margin: 0;
  padding: 0;
  list-style: none;
}

footer .quick-link li {
  margin-bottom: 40px;
}

footer .quick-link li a {
  font-size: 1.1rem;
  text-decoration: none;
  color: #919191;
}

footer .quick-link li::before {
  content: "â– ";
  font-size: 1.2rem;
  margin-right: 20px;
  color: #919191;
}

footer h3 {
  margin: 0;
  margin-bottom: 30px;
  font-size: 1.5rem;
  color: #ffffff;
  text-transform: uppercase;
}

footer h2 {
  color: #a7a7a7;
}

footer .lastes-tweet p {
  margin-bottom: 80px;
}

footer .lastes-tweet p span {
  color: #a7a7a7;
  font-weight: bold;
}

.end-footer {
  font-family: "Roboto Condensed", sans-serif;
  background-color: #061124;
  padding: 0;
  margin: 0;
  color: #919191;
  padding: 35px;
}

.end-footer .wrap-footer {
  display: flex;
  justify-content: space-between;
}

.end-footer .wrap-footer img {
  height: 25px;
}

.section1 img {
  width: 100%;
}

.menu-mobile {
  display: none;
}

@media only screen and (max-width: 1179px) {
  /* .wrap-header .navbar {
    display: none;
  } */

  .header .wrap-logo {
    margin-right: 20px !important;
  }

  .carousel img {
    width: 400px !important;
  }

  .section1 img {
    width: 60%;
    margin: 30px auto;
    display: block;
  }

  .section3 p {
    width: 90%;
  }
}

.toggle-navbar {
  display: none;
}

@media only screen and (max-width: 800px) {
  .cart-box {
    padding: 10px;
    margin-left: 10px;
    font-size: 0.9rem !important;
    bottom: 0px !important;
    position: static;
  }
  .header {
    position: relative;
    z-index: 3;
  }
}

@media only screen and (max-width: 500px) {
  .wrap-header {
    padding: 17px;
    position: fixed;
    z-index: 3;
    width: 100%;
    top: 0;
  }
  .wrap-header .navbar {
    display: none;
  }
  .cart-box {
    position: static;
    padding: 12px 8px;
    margin-left: 10px;
    font-size: 0.9rem !important;
    z-index: -1;
  }
  .header {
    position: relative;
    /* z-index: 3; */
  }
  .toggle-navbar {
    display: block;
    margin-left: auto !important;
    border: 1px solid #333;
    padding: 10px;
    z-index: 4;
    background-color: #fff;
  }
  .menu-mobile {
    display: block !important;
    position: absolute;
    top: 100%;

    background-color: aliceblue;

    width: 100%;
    max-height: 0;
    overflow-y: auto;
    margin-top: 10px;
    box-shadow: 5px 10px 18px #88888899;
    transition: max-height 0.2s;
    z-index: 4;
  }
  .menu-mobile.active {
    max-height: 300vh;
  }
  .overlay-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 150vw;
    background-color: #262626c2;
    transition: 0.2s;

    z-index: -1;
    height: 0;
  }
  .menu-mobile.active + .overlay-bg {
    height: 150vh;
  }

  .menu-mobile > ul {
    z-index: 4;
    margin: 0;
    list-style: none;
    padding: 0;
    background-color: #f8f8f899;
  }
  .menu-mobile ul.menu-mobile-subchild {
    max-height: 0px;
    overflow: hidden;
    transition: max-height 0.3s;
  }
  .menu-mobile ul.menu-mobile-subchild li a {
    font-size: 0.6rem;
    letter-spacing: 0.5px;
  }
  .menu-mobile ul.menu-mobile-subchild.active {
    max-height: 100vh;
  }

  .menu-mobile li {
  }
  .menu-mobile li a {
    text-decoration: none;
    padding: 15px 20px;
    display: block;
    font-size: 0.7rem;
    text-transform: uppercase;
    color: #666666;
    font-weight: bold;
    border-top: 1px solid #cccccc;
    display: flex;
    letter-spacing: 0.5px;
  }
  .menu-mobile > ul li.hasSubchild > a::after {
    content: "+";
    margin-left: auto;
  }
  .menu-mobile li a:hover {
    color: #f3a395;
  }
  .container {
    padding-top: 130px;
  }
  .section1 {
    padding: 25px;
  }
  .section2 {
    padding: 25px;
  }
  .section3 {
    padding: 25px;
  }

  .carousel img {
    width: 300px !important;
  }

  .section1 {
    margin-top: 40px;
  }

  .section1 h1 {
    font-size: 1.4rem;
  }

  .section1 p {
    font-size: 1rem !important;
  }

  .section1 .question-contact {
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .section1 .question-content-number {
    width: 80%;
    text-align: center;
  }

  .section3 h1 {
    font-size: 1.4rem;
  }

  .section3 p {
    width: 100%;
  }

  .section3 button {
    width: 100%;
    font-weight: bold;
  }

  /* Product Item */
  .product-content-wrapper {
    flex-direction: column;
    font-size: 0.9rem;
  }

  footer {
    padding: 20px;
  }

  footer h3 {
    font-size: 1.2rem;
  }

  footer .quick-link li {
    margin-bottom: 20px;
  }

  footer .lastes-tweet p {
    margin-bottom: 20px;
  }
  .end-footer .wrap-footer {
    flex-direction: column;
  }

  .end-footer .wrap-footer p {
    margin-bottom: 40px;
  }
}

.search-product-page {
  font-family: "Roboto Condensed", sans-serif;
}

.search-product-page > h3 {
  font-weight: 500;
  border-bottom: 1px solid #f1f1f1;
  padding-bottom: 20px;
  letter-spacing: 0.4;
  font-size: 0.9rem;
}

.search-product-page .form-search {
  display: flex;
  flex-direction: row;
  margin: 50px 0px;
}

.search-product-page .search-input {
  padding: 12px 24px;
  font-family: "Roboto Condensed", sans-serif;
  background: #f1f1f1 none repeat scroll 0 0;
  color: #8e8d8d;
  outline: none;
  border: none;
  font-weight: 300;
  letter-spacing: 0.6px;
  width: 100%;
}

.search-product-page .btn-submit-search {
  padding: 9px 18px;
  outline: none;
  border: none;
  background-color: #ffb52f;
}

.search-product-page .list-category {
  padding: 0;
  margin: 0;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 300;
  list-style: none;
  margin-bottom: 50px;
}

.search-product-page .list-category li {
  margin-bottom: 15px;
}

.search-product-page .list-category li a {
  color: #111111;
  text-decoration: none;
  font-size: 0.9rem;
  transition: 0.5s;
}

.search-product-page .list-category li a:hover {
  color: #ffb52f;
}

.search-product-page .form-pricing {
  display: flex;
  flex-direction: column;
  font-weight: 300;
  letter-spacing: 0.5;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 0.9rem;
  color: #828282;
}

.search-product-page .form-pricing input {
  padding: 9px 18px;
  width: 80px;
}

.search-product-page .filter {
  padding: 9px 18px;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: bold;
  background-color: #001232;
  color: #fff;
  border: none;
  outline: none;
  width: 100%;
  margin: 30px 0px;
  text-transform: uppercase;
}

.search-product-page .filter:hover {
  background-color: #ffb52f;
}

.search-product-page .group-color {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 50px;
}

.search-product-page .group-color .color-item {
  width: 20px;
  height: 20px;
  background-color: #ffb52f;
  border-radius: 50%;
  margin: 10px 10px 10px 0px;
}

.search-product-page .group-tag {
  display: flex;
  flex-wrap: wrap;
}

.search-product-page .group-tag .tag {
  padding: 5px 10px;
  border: 1px solid #ebebeb;
  margin: 10px 10px 10px 0px;
  cursor: pointer;
  transition: 0.3s;
  font-weight: 300;
}

.search-product-page .group-tag .tag:hover {
  background-color: #ffb52f;
  color: #f1f1f1;
}

.search-product-page .pagination {
  display: flex;
  justify-content: flex-end;
  margin: 30px 0px 30px 0px;
}

.list-search-product {
  padding-left: 30px;
}

.search-product-page .filter-bar {
  display: flex;
  padding: 10px 16px;
  align-items: center;
}

.search-product-page .filter-bar .left-side {
  display: flex;
  font-size: 1.2rem;
}

.search-product-page .filter-bar .left-side div {
  margin-right: 20px;
}

.search-product-page .filter-bar .left-side div.active {
  color: #ffb52f;
}

.search-product-page .right-side {
  margin-left: auto;
}

.search-product-page .right-side select {
  padding: 10px;
  font-family: "Roboto Condensed", sans-serif;
  background-color: #eeeeee;
  border: none;
  outline: none;
}

.search-product-page .right-side select option {
  padding: 10px;
  font-family: "Roboto Condensed", sans-serif;
}

@media only screen and (max-width: 1280px) {
  .list-search-product {
    padding-left: 0px;
  }
}

@media only screen and (max-width: 500px) {
  .search-product-page .product-content-wrapper {
    flex-direction: column;
  }

  .search-product-page .product-content-wrapper .product-title-spreed {
    text-align: center;
  }
}

.product-detail-page {
  font-family: "Roboto Condensed", sans-serif;
}

.product-detail-page .image-preview-container {
  margin-top: 50px;
}

.product-detail-page .image-preview-container .img-preview-big {
  width: 100%;
}

.product-detail-page .carousel-container .img-preview-small {
  width: calc(25% - 10px);
  margin: 5px;

  cursor: pointer;
  box-sizing: border-box;
  transition: 0.1s;
}

.product-detail-page .carousel-container .img-preview-small.selected {
  border: 3px solid #ffb52f;
  /* transform: scale(1.1); */
}

.product-detail-page .carousel-container {
  position: relative;
}

.product-detail-page .carousel-container .btn-carousel {
  position: absolute;
  width: 35px;
  height: 35px;

  background-color: white;
  color: gray;

  top: 50%;
  transform: translateY(-50%) translateX(-50%);

  border: none;
  border-radius: 50%;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);

  text-align: center;
  font-family: "Courier New", Courier, monospace;
  font-weight: bold;
  font-size: 1.5em;

  transition: 0.3s;
  cursor: pointer;

  z-index: 3;
  outline: none;
}

.product-detail-page .carousel-container .btn-carousel:hover {
  background-color: #ffb52f;
  color: white;
}

.product-detail-page .product-detail-content {
  margin-top: 50px;
}

.product-detail-page .product-detail-content h2 {
  color: #353535;
  font-size: 24px;
  font-weight: bold;
  margin: 0;
}

.product-detail-page .product-detail-content .quick-view-rating {
  display: flex;
  align-items: center;
}

.product-detail-page .product-detail-content .quick-view-rating p {
  font-size: 14px;
  font-style: normal;
  color: #333333;
}

.product-detail-page .product-price span {
  color: #353535;
  font-size: 24px;
  font-weight: bold;
  line-height: 1;
  margin-top: 2px;
  display: inline-block;
}

.product-detail-page .product-detail-content .sub-title {
  color: #333;
  display: block;
  font-size: 16px;
  letter-spacing: 0.3px;
  margin-bottom: 15px;
  padding-bottom: 10px;
  position: relative;
}

.product-detail-page .product-detail-content .sub-title::before {
  background-color: #9d9d9d;
  bottom: 0;
  content: "";
  height: 1px;
  left: 0;
  position: absolute;
  width: 30px;
}

.product-detail-page .product-detail-content .product-color {
  margin: 31px 0 35px;
}

.product-detail-page .product-detail-content ul {
  display: flex;
  flex-direction: row;
  margin: 0px;
  padding: 0px;
}

.product-detail-page .product-detail-content .product-color > ul li {
  border-radius: 50px;
  cursor: pointer;
  display: block;
  height: 21px;
  width: 21px;
  margin-right: 15px;
  text-indent: -9999px;
  transition: 0.1s;
}

.product-detail-page .product-detail-content .product-color > ul li:hover {
  transform: scale(1.25);
}

.product-detail-page .product-detail-content .product-categories li {
  margin-right: 15px;
  display: block;
}

.product-detail-page .product-detail-content .product-categories li > a {
  color: #333;
  text-decoration: none;
  text-transform: capitalize;
  font-weight: 500;
}

.product-detail-page .product-detail-content .product-categories li > a:hover {
  color: #ffb52f;
}

.product-detail-page .product-detail-content .product-action {
  display: flex;
  justify-content: flex-start;
  padding-top: 5px;
}

.product-detail-page .product-detail-content .product-action .cart-plus-minus {
  border: 1px solid #dcdcdc;
  overflow: hidden;
  padding: 12px 0 7px 5px;
  width: 80px;
  height: 46px;
}

.product-detail-page .product-detail-content .product-action .cart-plus-minus div {
  color: #727272;
  cursor: pointer;
  float: left;
  font-size: 16px;
  font-weight: 600;
  height: 20px;
  line-height: 20px;
  position: relative;
  text-align: center;
  width: 20px;
}

.product-detail-page .product-detail-content .product-action .cart-plus-minus input {
  background: transparent none repeat scroll 0 0;
  border: medium none;
  float: left;
  font-size: 16px;
  height: 25px;
  margin: 0;
  padding: 0;
  text-align: center;
  width: 25px;
}

.product-detail-page .product-detail-content .product-action .btn-add-to-cart {
  background-color: #ffb52f;
  color: #ffffff;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  letter-spacing: 1px;
  line-height: 18px;
  padding: 14px 33px 12px;
  text-transform: uppercase;
  border-radius: 0;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out 0s;
  margin: 0 30px;
}

.product-detail-page .product-detail-content .product-action .btn-add-to-cart:hover,
.product-detail-page .product-detail-content .product-action .btn-wish-list:hover {
  color: white;
  background-color: #001232;
}

.product-detail-page .product-detail-content .product-action .btn-wish-list {
  border: 1px solid #dcdcdc;
  color: #727272;
  font-size: 22px;
  padding: 7px 18px 5px;
  transition: all 0.2s ease-in-out 0s;
  cursor: pointer;
}

.checkout-page {
  font-family: "Roboto Condensed", sans-serif;
  padding: 10px;
  background-color: #f5f5f5;
}

/* panel */
.checkout-page .panel-body {
  padding: 10px 15px 0px;
  background-color: #fff;
}

/* panel order title */
.checkout-page .panel-body .order-title {
  position: relative;
  border-bottom: 1px solid #c9c9c9;
  padding: 10px 0;
}

.checkout-page .panel-body .order-title .btn-edit {
  position: absolute;
  padding: 10px 20px;

  right: 0;
  top: 50%;
  transform: translateY(-50%);

  border: none;
  cursor: pointer;
  background-color: #e5e5e5;
}

.checkout-page .panel-body .order-title .btn-edit:hover {
  background-color: var(--primary-bg);
  color: white;
}

/* panel list products */
.checkout-page .panel-body .products {
  margin-bottom: 15px;
}

.checkout-page .panel-body .products .item {
  position: relative;
  padding-top: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid #c9c9c9;
  font-size: 13px;
}

.checkout-page .panel-body .item .title {
  padding-right: 150px;
}

.checkout-page .panel-body .item p.title strong {
  padding-right: 10px;
}

.checkout-page .panel-body .item p.title a {
  color: #007ff0cc;
  text-decoration: none;
}

.checkout-page .panel-body .item p.title a:hover {
  text-decoration: underline;
}

.checkout-page .panel-body .item p.title .seller-by {
  display: block;
  float: none;
  margin-top: 5px;
}

.checkout-page .panel-body .item .price {
  float: right;
  position: absolute;
  right: 0;
  top: 10px;
  text-align: right;
}

.checkout-page .panel-body .item .price span {
  display: block;
  float: none;
}

/* list info price */
.checkout-page .panel-body .list-info-price {
  margin-bottom: 13px;
}

.checkout-page .panel-body .list-info-price span {
  float: right;
}

/* total */
.checkout-page .panel-body .total {
  border-top: 2px solid #c9c9c9;
  margin-bottom: 0;
  line-height: 19px;
  font-weight: 700;
  padding: 20px 0;
}

.checkout-page .panel-body .total span {
  float: right;
  color: #ee2347;
  font-size: 1.75em;
  font-weight: 400;
}

.form-custom {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #ffffff;
  padding: 20px;
  width: 80%;
  font-family: "Roboto Condensed", sans-serif !important;
  margin: 30px 0px;
}

.form-custom .MuiAppBar-colorPrimary {
  background-color: #ffffff;
  color: #2196f3;
}

.form-custom .MuiTab-wrapper {
  font-family: "Roboto Condensed", sans-serif;
}

.form-custom .MuiTabs-indicator {
  background-color: #2196f3;
}

.form-custom .form-control {
  margin: 40px 0px;
}

.MuiFormLabel-root {
  font-family: "Roboto Condensed", sans-serif !important;
  font-weight: 300;
}

.MuiInputBase-input {
  font-family: "Roboto Condensed", sans-serif !important;
}

.form-custom .login-button {
  width: 100%;
  background-color: #2196f3;
  color: #ffffff;
  font-weight: bold;
  font-family: "Roboto Condensed", sans-serif !important;
}

.form-custom .login-button:hover {
  background-color: #2196f3;
}
.form-custom .MuiFormGroup-root {
  flex-direction: row;
}

.form-custom .label {
  font-weight: bold;
}

.form-custom .img-form-login {
  width: 100%;
}

.modal-login {
  overflow-y: scroll;
}

@media only screen and (max-width: 900px) {
  .form-custom {
    width: 100%;
  }
}

.information-page > * {
